<script>
import $ from 'jquery'
window.$ = $
import 'jquery-ui/ui/effect'

export default {
  name: 'Nav',
  methods: {
        handleScroll: function (event) {
          const scrollPos = $(window).scrollTop();
          if (scrollPos > 10) {
            $('#logo').switchClass('high', 'low');
            $('#nav').switchClass('weg', 'da');
          } else {
            $('#logo').switchClass('low', 'high');
            $('#nav').switchClass('da', 'weg');
          }
        }
    },

    created: function () {
        window.addEventListener('scroll', this.handleScroll);
    },
    destroyed: function () {
        window.removeEventListener('scroll', this.handleScroll);
    }
}
</script>

